@mixin font($size, $weight) {
  font-size: $size;
  font-weight: $weight;
}

@mixin absolute($top: initial, $right: initial, $bottom: initial, $left: initial, $width: "", $height: "") {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
  width: $width;
  height: $height;
}

@mixin flex($flex-direction: row, $justify-content: flex-start, $align-items: stretch) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin scrollBar($type: vertical, $wh: 6px, $track-color: $c2, $thumb-color: $c1) {
  &::-webkit-scrollbar {
    width: $wh;
    height: $wh;
  }
  &::-webkit-scrollbar-track {
    background: $track-color;
  }
  &::-webkit-scrollbar-thumb {
    background: $thumb-color;
    border-radius: 9999px;
  }
  @if ($type == vertical) {
    overflow-x: hidden;
    overflow-y: scroll;
  } @else {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}

@mixin textEllipsis($line-clamp: 2) {
  overflow: hidden;
  text-overflow: ellipsis;
  @if ($line-clamp >= 2) {
    display: -webkit-box;
    -webkit-line-clamp: $line-clamp;
    -webkit-box-orient: vertical;
  } @else {
    display: block;
    white-space: nowrap;
  }
}

@mixin icon($icon, $wh: 40px, $color1: "000", $color2: "fff") {
  position: relative;
  display: block;
  width: $wh;
  height: $wh;
  white-space: nowrap;
  text-indent: 9999px;
  overflow: hidden;
  &::before {
    content: "";
    @include absolute($left: 0, $top: 0, $width: $wh, $height: $wh);
    background: icon($icon, $color1, $color2) center / 100% no-repeat;
  }
}

@keyframes opacity0to0 {
  from {
    opacity: 1;
    transform: rotate(0deg);
  }
  50% {
    opacity: 0;
    transform: rotate(180deg);
  }
  to {
    opacity: 1;
    transform: rotate(360deg);
  }
}
